@import 'yoda-core-components/lib/assets/styles/base/base.css';

    .saveForLater {
        padding: 0px;
        z-index: 2;
        color: #1c2024;
        font-size: 14px;
        text-align: center;
        & .saveTooltipBottom {
            min-width: 152px;
            display: inline-block;
            position: absolute;
            background: #f5f5f5;
            box-shadow: 0 8px 16px 0 rgba(83, 96, 107, 0.5);
            right: 8px;
            top: 55px;
            padding: 10px;
            border-radius: 10px;
            border: solid 1px #c7cdd4;
            min-height: 50px;
            z-index: 2;
        }
        & .saveTooltipBottom:before {
            content: "";
            display: block;
            position: absolute;
            width: 0px;
            height: 0px;
            border-style: solid;
            border-color: transparent;
            border-width: 10px;
            top: -10px;
            margin-left: -10px;
            border-top-width: 0px;
            border-bottom-color: #f5f5f5;
            right: 9px;
            z-index: 2;
        }
        & .saveTooltipRight {
            min-width: 160px;
            display: inline-block;
            position: absolute;
            background: #f5f5f5;
            box-shadow: 0 8px 16px 0 rgba(83, 96, 107, 0.5);
            left: 100%;
            padding: 10px;
            border-radius: 10px;
            margin-left: 8px;
            border: solid 1px #c7cdd4;
            min-height: 50px;
            z-index: 2;
        }
        & .saveTooltipRight:before {
            content: "";
            display: block;
            position: absolute;
            width: 0px;
            height: 0px;
            border-style: solid;
            border-color: transparent;
            border-width: 10px;
            left: -10px;
            margin-top: -10px;
            border-left-width: 0px;
            border-right-color: #f5f5f5;
            top: 18px;
            z-index: 2;
        }

        & .sflButtonStyle {
            background: #fff;
            border-radius: 20px;
            opacity: 0.9;
            width: 40px;
            height: 40px;
        }

        & .extraMargin {
            margin: 8px 8px 0 0;
        }

        & .success {
            font-size: 14px;
            font-weight: bold;
            line-height: 1.71;
            text-align: center;
            color: #01803f;
        }
        & .error {
            font-size: 14px;
            font-weight: bold;
            line-height: 1.71;
            text-align: center;
            color: #a2000f;
        }
        & .link {
            font-size: 14px;
            line-height: 1.71;
            text-align: left;
            color: #006c7c;
            text-decoration: underline;
        }
    }
    


