@import 'yoda-core-components/lib/assets/styles/base/base.css';
@import 'yoda-core-components/lib/assets/styles/grid/grid.css';

.coupon-container {
    & .noPad {
       padding: 0;
       margin: 0;
    }

    & .noMargin {
        margin: 0;
    }

    & .couponHeader {
        margin-bottom: 8px;

         & .couponTitle {
            font: bold 32px/40px var(--main-font);
            color: var(--color-gray-7);
            vertical-align: top;

            @media(--viewport-sm) {
                font-size: 24px;
                line-height: 32px;
                margin-bottom: 16px;
            }
        }

        & .boxModel {
            border: 0px;
            text-align: center;
            vertical-align: middle;
        }
    }

    & .productCartridge {
        margin-top:16px;
    }

    & .adPromo {
            text-align: center;
    }

    & .couponSeoContent {
        margin-top: 16px;
        margin-bottom: 16px;
    }

    & .couponBody {
        margin-top: 24px;
        margin-bottom: 16px;


        & .errorHeading {
            font-size: 2.5rem;
            color: var(--color-red);
            margin: 0;
        }

        & .errorText {
            font-size: .95rem;
            line-height: 1.5;
            margin-bottom: 1.25rem;
        }

        & .errorSuggestions {
            padding-left: .625rem;
            font-size: .95rem;
        }
    }
}

/* SEO Content Block Styles */
.seoContent {
    font: normal 14px/24px var(--open-sans);
    color: var(--color-gray-7);

    & .seoToggleBtn {
        margin-top: 12px;

        @media (--viewport-lg-plus) {
            display: none;
        }
    }

    & .seoContentHeight {
        overflow: hidden;
        height: 15.8rem;

        @media (--viewport-md) {
            height: 6.8rem;
        }

        @media (--viewport-lg-plus) {
            height: auto;
        }
    }

    & .seoContentTitle, & h2 {
        font: bold 24px/32px var(--main-font) !important;
        color: var(--color-gray-7) !important;
        margin-bottom: 8px;
    }

    & a {
        color: var(--color-seconday-turquoise);
        text-decoration: underline;
    }
}
/* SEO Content Block Styles Ends */


