@import 'yoda-core-components/lib/assets/styles/base/base.css';
@import 'yoda-core-components/lib/assets/styles/grid/grid.css';

.toolTipCont {
	position: absolute;
	width: 300px;
    background-color: var(--color-white);
    box-shadow: 0 6px 11px 0 rgba(83, 96, 107, 0.5);
    border: solid 1px var(--color-gray-3);
    left: -322px;
	bottom: -128px;
    z-index: 9999;
    padding-bottom: 15px;
	&:after, &:before {
		left: 100%;
		top: 50%;
		border: solid transparent;
		content: " ";
		height: 0;
		width: 0;
		position: absolute;
		pointer-events: none;
	}

	&:after{
		border-left-color: var(--color-white);
		border-width: 10px;
		margin-top: -10px;
	}

	&:before{
		border-left-color: var(--color-gray-3);
		border-width: 11px;
		margin-top: -11px;
	}

	& .subContainer{
		padding: 10px 10px 0 10px;

		& .header{
			text-align: left;
			& .text{
				display: inline-block;
				width: 90%;
				text-align: left;
				vertical-align: top;
				color: var(--color-gray-7);
				font-size: 16px;
			}

			& .closeToolTip{
				position: absolute;
			    right: 8px;
			    top: 7px;
				text-align: center;
				@media (--viewport-sm){
					display: none;
				}
				& .close{
					width: 22px;
				}
			}
		}

		& .bodyContainer{
			min-height: 166px;
			margin: 10px;
			padding-bottom: 10px;
			padding-left: 10px;
			& .itemsContainer {
				width: 100%;
				position: relative;
				margin-top: 16px !important;
				margin-top: 8px!important;
    			display: inline-block;
				& .radioButton{
					display: inline;
					float: left;
					width:30px;
				}

				& .itemName{
					display: inline;
					float: left;
				    padding-left: 10px;
				    vertical-align: middle;
					color: var(--color-gray-6);

					& .imageTag {
						width: 25px;
					}
				}
			}
		}

		& .createnewListCont {
			display: inline-block;
			width: 100%;
			margin-top: 20px;
			margin-bottom: 10px;
			& .heading {
				font-size: 14px;
					font-weight: bold;
					color: var(--color-gray-6);
					text-align: left;
			}

			& .inputContainer {
				display: inline-block;
				width: 60%;

				& .input {
					display: block;
				    width: 100%;
				    color: #1c2024;
				    height: 46px;
				    opacity: .75;
				    font-size: 16px;
				    padding: 0 16px;
				    border: 1px solid #c7cdd4;
				    outline: none;
				    box-shadow: 1px 1px 2px 0 rgba(28,0,0,.6);
				}
			}

			& .moveButton{
				display: inline-block;
				width: 40%;

				& .button {
					min-width: 109px !important;
					padding: 11px 0 !important;
				}
			}
		}

		& .bodyContainerFH {
			max-height: 200px !important;
		}

		& .footerContainer{
			border-top: 1px solid var(--color-gray-3);
			padding-top: 6px;
			text-align: left;
			padding-left: 10px;
			& .button {
				text-decoration: none;
				font-size: 14px;
			}
		}
	}
	& .btnLInk{
		color: #006c7c;
		font-weight: bold;
		text-decoration: none;
	}
	& .successtext{
		color: #01803f;
    	font-weight: bold;
	}
}



