@import 'yoda-core-components/lib/assets/styles/colors/colors.css';
@import 'yoda-core-components/lib/assets/styles/grid/grid.css';
@import 'yoda-core-components/lib/assets/styles/fonts/fonts.css';


/*Custom Themes*/
@import './themes/YodaTheme.css';
@import './themes/CartridgeTheme.css';
@import './themes/OneRecordBannerTheme.css';

:root {
    /*Global fonts*/
    --smallFont: 0.75rem;
    --divider-gray: #979797;
}

.productCardFinal {
    padding: 0 4px;
    max-height: 100%;

    & .badge {
        display: inline-block;
        position: absolute;
        top: 12px;
        left: -5px;
        padding: 5px;
        background: var(--color-white);
        border-radius: 0px 3px 3px 0px;
        font-weight: bold;
        z-index: 1;
        box-shadow: 0 2px 4px 0 rgba(83, 96, 107, 0.5);
        border: solid 1px var(--color-gray-3);
        font-family: var(--open-sans);
        font-size: 0.75rem;
    }

    & .extendedSize {
        display: inline-block;
        width: 100%;
        position: absolute;
        bottom: -1px;
        background: var(--color-white);
        opacity: 0.9;
        font-size: 14px;
        left: 0px;
        font-family: var(--open-sans);
    }

    & .badgechild:before {
        content: ' ';
        width: 0;
        height: 0;
        border-left: 4px solid transparent;
        border-right: 0 solid transparent;
        border-bottom: 5px solid var(--divider-gray);
        top: -6px;
        position: absolute;
        z-index: -1;
        left: -1px;
    }

    & .redBadge {
        color: var(--color-red)
    }

    & .greenBadge {
        color: var(--color-green)
    }

    & .blackBadge {
        color: var(--color-black)
    }

    & .blueBadge {
        color: var(--color-blue)
    }

    & .card {
        box-shadow: none;
        height: auto;
    }

    & .container {
        width: 100%;
    }

    & .cardWrapper {
        border-radius: 4px;
        background-color: var(--color-white);
        border: solid 1px var(--color-gray-8);
        height: 100%;
    }
    & .galleryProduct {
        font-family: var(--main-font);
        display: inline-block;
        width: 13.5rem;
        padding: 0 0.5rem;
    }
    & .title {
        font: 500 var(--smallFont) var(--open-sans);
        color: var(--color-gray-7);
        line-height: 1.5rem;
        margin: 8px 0 6px;
        padding: 0;
        height: 45px;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }

    & .imgBlock {
        text-align: center;
        display: inline-block;
        width: 100%;
        position: relative;
        /*border-bottom: 1px solid var(--color-gray-8);*/
        & .imageClass {
            max-width: 100%;
            max-height: 100%;
        }

        & .imageDetails {
            height: 231px;
            display: flex;
            display: -webkit-flex; /* For lower iPad Version */
            justify-content: center;
            align-items: center;
        }
    }

    & .detailsWrapper {
        padding: 0.5rem 1rem 1rem;
        display: inline-block;
        vertical-align: top;
        width: 100%;

        & .colorSwatch {
            font-size: 0.75rem;
            line-height: 1.33;

            & .colorSwatchText {
                color: var(--color-dark-gray);
                font-family: var(--open-sans);
            }
        }

        & .ecoRebate {
            font-family: var(--open-sans);
            & .link {
                font-size: 0.75rem;
                color: var(--color-seconday-turquoise);
                text-decoration: underline;
            }
        }
    }


    & .grid {
        & .imgBlock {
            width: 100%;
            min-height: 150px;
            border-bottom: 1px solid var(--color-gray-8);
        }

        & .detailsWrapper {
            width: 100%;
        }
    }

    & .list {
        display: flex;
        display: -webkit-flex; /* For lower iPad Version */

        & .imgBlock {
            border-right: 1px solid var(--color-gray-8);
        }
    }

    & .titleLink {
        text-decoration: none;
        color: var(--color-light-black);
        text-transform: capitalize;
        font-size: 0.75rem;
        line-height: 1rem;
        font-weight: 500;
    }

    & .pricingWrapper {
        padding-top: 5px;
    }

    & .ratingWrapper {
        min-height: 25px;
        
        & .ratingContainer{
            display: inline-block;

            & .rating{
                overflow: visible;
            }
        }
        & .reviewCount {
            color: var(--color-seconday-turquoise);
            font-size: var(--smallFont);
            font-weight: bold;
            padding: 0 0.2rem;
            font-family: var(--open-sans);
        }
    }
}

@media (--viewport-md) {
    .productCardFinal {
        & .imgBlock {
            & .imageDetails {
              height: 278.8px;
            }
        }
    }
}

@media (--viewport-sm) {
    .productCardFinal {
        & .imgBlock {
            & .imageDetails {
              height: 238px;
            }
        }
        & .list {
            & .title{
                font-size: 14px;
                line-height: 20px;
                height: 43px;
            }
        }
    }
}

.rebateModalBlock {
    margin-top: 0;
}


