@import 'yoda-core-components/lib/assets/styles/colors/colors.css';
@import "yoda-core-components/lib/assets/styles/settings/breakpoints.css";
@import 'yoda-core-components/lib/assets/styles/grid/grid.css';

:root {
    --arrow-border-color: #a9b9b9;
    --arrow-disabled-background: #e3e6ea;
    --arrow-margin: 24px;
    --arrow: {
        position: absolute;
        width: 45px;
        height: 43.8px;
        border-radius: 50%;
        background-color: var(--color-white);
        box-shadow: 0 2px 4px 0 rgba(117, 117, 117, 0.5);
        border: solid 2px var(--arrow-border-color);
        z-index: 2;
        margin: auto;
        cursor: pointer;
        bottom: 0;
        top: 0;

        &:disabled {
            box-shadow: 0 2px 4px 0 rgba(117, 117, 117, 0.5);
            cursor: default;
            border: none;
        }
    };
    --device-wrapper: {
        & .wrapper {
            & .productCartridgeTitle {
               font-size: 1.25rem;
            }
        }
    };
}

.productCartridge {
    & .wrapper {
        width: 100%;
        padding: 5px 0 13px;
        position: relative;

        & .productCartridgeTitle {
            padding: 0px 8px 4px 0;
            font-size: 1.5rem;
            font-weight: bold;
            color: var(--color-gray-6);
        }

        & .prodCatridgeListBlock {
            margin: 0;
        }

        & img {
            max-width: 100%;
        }

        & .linkColor {
            color: var(--color-light-black);
            display: inline-block;
            width: 100%;
            display: inherit;

            & .productCard{
                width: 100%;
            }
        }

        & .leftArrow {
            @apply --arrow;
            left: 0;
            margin-left: var(--arrow-margin);
        }

        & .rightArrow {
            @apply --arrow;
            right: 0;
            margin-right: var(--arrow-margin);
        }

        & .listStyleClass {
            display: flex;
            transition: transform .4s ease-in-out;
        }

        & .itemStyleClass {
            padding: 0px;
            display: inherit;
            flex-shrink: 0;
            overflow: hidden;

            @media (--viewport-md) {
                width: 49%; /* Applied to get as per the mocks */
            }

            @media (--viewport-lg-plus) {
                width: 24.68%; /* Applied to get as per the mocks */
            }

            &.compact {
                @media (--viewport-sm) {
                    width: 41.5%;
                }

                @media (--viewport-md) {
                    width: 32.5%; /* Applied to get as per the mocks */
                }

                @media (--viewport-lg) {
                    width: 19%; /* Applied to get as per the mocks */
                }

                @media (--viewport-xl) {
                    width: 13.5%; /* Applied to get as per the mocks */
                }
            }
        }

        & .desktopBodyClass {
            overflow: hidden;
        }

        & .mobileBodyClass{
            overflow-y: hidden;
            overflow-x: scroll;
            -webkit-overflow-scrolling: touch;
        }

        & .iconClass{
            fill: var(--color-seconday-turquoise);
            color: var(--color-seconday-turquoise);
        }

        & .disabledArrow{
            fill: var(--color-gray-3);
            color: var(--color-gray-3);
        }
    }

    @media (--viewport-sm) {
        @apply --device-wrapper;
    }

    @media (--viewport-md) {
        @apply --device-wrapper;
    }

}

.productPanelLinkBlock {
    border-radius: 4px;
    background-color: var(--color-white);
    border: solid 1px var(--color-gray-8);
    margin: 0 3px;

    & .productCard {
        padding: 0;
    }
}

@media (--viewport-sm) {
    .productCatridge {
        padding: 0;

        & [class*="cardWrapper"] {
            & [class*="detailsWrapper"] {
                & [class*="pricingWrapper"] {
                    min-height: 34px;

                    & [class*="cardSummaryLabel"] {
                        display: none;
                    }
                }
            }
        }
    }
}



