@import 'yoda-core-components/lib/assets/styles/grid/grid.css';
@import 'yoda-core-components/lib/assets/styles/colors/colors.css';
@import 'yoda-core-components/lib/assets/styles/fonts/fonts.css';
@import "yoda-core-components/lib/assets/styles/settings/breakpoints.css";

/* Hot Deals */
.hotDealsBlock {
    padding-bottom: 16px;
}
.hotDeals {
    margin: 16px 0;
    height: 88px;
    position: relative;
    background: var(--color-gray-13);

    & .hotDealsInfo {
        width: 100%;
        height: 100%;
        display: table;
        table-layout: fixed;
        text-align: center;

        @media (--viewport-sm) {
            text-align: left;
        }
        
        & .hotDealsTextInfo {
            display: table-cell;
            vertical-align: middle;
            text-transform: uppercase;
            padding: 0 16px;

            & .hotDealsMainText, & .hotDealsSubText {
                font: bold 24px var(--open-sans);
                color: var(--color-white);
                display: inline-block;
                vertical-align: middle;

                @media (--viewport-sm) {
                    font-size: 18px;
                }
            }
        }

        & .hotDealsCta {
            display: table-cell;
            vertical-align: middle;
            width: 200px;
            padding-right: 16px;

            @media (--viewport-sm) {
                width: 160px;
            }

            & .hotDealsLink {
                display: block;
                background: var(--color-white);
                border: 1px solid var(--color-gray-3);
                color: var(--color-gray-6);
                font: 700 14px var(--main-font);
                text-align: center;
                border-radius: 2px;
                box-shadow: 0 2px 4px 0 rgba(83, 95, 107, 0.5);
                min-width: 120px;
                padding: 4px;
                line-height: 24px;
                
                &:hover {
                    box-shadow: 0 4px 8px 0 rgba(83, 95, 107, 0.5);
                    background-color: var(--color-gray-0);
                }

                &:active {
                    background-color: var(--color-gray-8);
                    color: var(--color-gray-7)
                }

            }

            & .viewAllDeals {
                width: 150px;

                @media (--viewport-sm) {
                    width: 128px;
                }
            }
        }
    }
}

@media (--viewport-lg-plus) {
    .hotDealsBlock {
        display: table;
        width: 100%;
        table-layout: fixed;
        position: relative;
        padding: 16px 0;

        & .hotDeals {
            display: table-cell;
            vertical-align: top;
            width: 182px;
            height: 100%;

            & .hotDealsTextInfo {
                padding: 0;
                width: 100%;
                height: 100%;
                padding-bottom: 66px;

                & .hotDealsMainText {
                    font-size: 34px;
                }

                & .hotDealsSubText {
                    margin-top: 15px;
                    padding: 0;
                }
            }

            & .hotDealsCta {
                position: absolute;
                bottom: 0;
                width: 100%;
                left: 0;
                padding: 16px 8px;
            }
        }
    }
}
.linkColor {
    display: block;
    width: 100%;
}
/* Hot Deals Ends */

.productPanelLinkBlock {
    border-radius: 4px;
    background-color: var(--color-white);
    border: solid 1px var(--color-gray-8);
    margin: 0 3px;
    overflow: hidden;

    & .productCard {
        padding: 0;
    }
}



