@import "../../assets/styles/colors/colors.css";

.carousel {
    overflow: hidden;

    & .carouselBlock {
        position: relative;
    }

    & .carouselContainer {
        transition: margin-left 0.3s ease-in-out;
    }

    & .carouselList {
        padding: 0;
        margin: 0;
        table-layout: fixed;

        & img {
            width: 100%
        }
    }

    & .carouselBullets {
        text-align: center;
        background: var(--color-light-white);
    }

    & .carouselContainer{
       width: 100%;
       overflow: hidden;
    }

    & .bullet {
        background: var(--color-light-gray);
        border-radius: 50%;
        height: 8px;
        width: 8px;
        opacity: 0.7;
        cursor: pointer;
        margin: 10px 5px;
        display: inline-block;

        &:hover {
            opacity: 1;
        }

        &.selected {
            opacity: 1;
            background: var(--color-red);
        }
    }

    & .nextWrapper,
    & .previousWrapper {
        position: absolute;
        top: 50%;
        width: 26px;
        text-align: center;
        background: rgba(255, 255, 255, .5);
        transform: translateY(-50%);
    }

    & .next,
    & .previous {
        display: block;
        color: var(--color-white);
        font-size: 1.5rem;
        text-decoration: none;
        cursor: pointer;
        line-height: 1
    }

    & .nextWrapper {
        right: 0
    }

    & .previousWrapper {
        left: 0
    }

    & .bottomContainer {
       display: flex;

       & .bulletContainer{
           width: 80%;
       }

       & .slotContainer {
            width: 10%;
       }
    }
}



