@import 'yoda-core-components/lib/assets/styles/grid/grid.css';
@import 'yoda-core-components/lib/assets/styles/settings/breakpoints.css';

.productCardFinal{
    &.cartridge{
        & .card {
            height: 100%;
            border: none;

            @media (--viewport-sm) {
                & .imgBlock {
                    min-height: 114px;

                    & .imageDetails {
                        height: 114px;
                    }
                }

                & .detailsWrapper {
                    padding: 0.5rem;
                }

                & .title {
                    line-height: 16px;
                    height: 32px;
                    margin: 0;
                }

                & .prodCardStarRating {
                    font-size: 11px;
                }
                
                & .pricingWrapper{
                    min-height: 48px;

                    & .cardPriceLabel {
                        font-size: 12px;
                        line-height: 14px;
                    }

                    & .priceWrapperClass {
                        line-height: 16px;

                        & .sale {
                            font-size: 12px;
                        }
                    }
                }

                & .cardSummaryLabel {
                    margin: 3px 0;
                }
                
                & .reviewCount {
                    font-size: 11px;
                }

                & .customPriceMarketingLabel {
                    display: none;
                }
            }
       }

        & .pricingWrapper{
            min-height: 67px;
            padding-top: 0;

            & .priceWrapperClass{
                white-space: normal;
                line-height: 20px;
                margin: 5px 0;

                @media (--viewport-sm) {
                    margin: 0;
                    height: 34px;
                    overflow: hidden;
                }

                & .priceClass{
                    font-size: 1.25rem;
                    
                    @media (--viewport-sm) {
                        font-size: 13px;
                        display: block;
                    }
                }
            }

            .salesummary {
                white-space: nowrap;
            }
       }

        & .imgBlock {
            & .imageClass {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
}



