@import 'yoda-core-components/lib/assets/styles/colors/colors.css';

.priceWrapper{
  white-space: nowrap;
  & .price {
      font-size: 2rem;
      color: var(--color-gray-7);
      font-weight: bold;

      /*@media screen and (--viewport-md) {
          font-size: 25px;
      }

      @media screen and (--viewport-sm) {
          font-size: 13px;
      }*/
  }

  &.reducePriceFont{
      white-space: normal;
      & .price {
          font-size: 1.25rem;
      }
  }
}

.sale {
    font-size: 16px;
    font-weight: bold;
    color: #008a44;
    text-transform: lowercase;
}

.clearence {
    color: #cb0c0b;
}

.salesummary {
    font-size: 12px;
    line-height: 1.33;
    color: #53606b;

     & .percentageOff{
        display: inline-block;
    }
}

.marketingLabel {
    font-size: 12px;
    font-weight: bold;
    line-height: 1.33;
    color: #cb0c0b;

    &.mapPrice{
        font-size: 1.25rem;
    }
}



