@import 'yoda-core-components/lib/assets/styles/grid/grid.css';
@import 'yoda-core-components/lib/assets/styles/colors/colors.css';
@import "yoda-core-components/lib/assets/styles/settings/breakpoints.css";

:root {
    --arrow-border-color: #a9b9b9;
    --arrow-disabled-background: #e3e6ea;
    --arrow-margin: 24px;
    --arrow: {
        position: absolute;
        width: 45px;
        height: 43.8px;
        border-radius: 50%;
        background-color: var(--color-white);
        box-shadow: 0 2px 4px 0 rgba(117, 117, 117, 0.5);
        border: solid 2px var(--arrow-border-color);
        z-index: 10;
        margin: auto;
        cursor: pointer;
        bottom: 0;
        top: 0;

        &:disabled {
            box-shadow: 0 2px 4px 0 rgba(117, 117, 117, 0.5);
            cursor: default;
            border: none;
        }
    };
}

.catridgeBlock {
    & .wrapper {
        width: 100%;
        position: relative;
        padding-left: 5px;

        & .catridgeListBlock {
            margin: 0 -4px;
        }

        & img {
            max-width: 100%;
        }

        & .linkColor {
            color: var(--color-light-black);
            display: inline-block;
            width: 100%;
            display: inherit;

            & .productCard{
                width: 100%;
            }
        }

        & .leftArrow {
            @apply --arrow;
            left: 0;
            margin-left: var(--arrow-margin);
        }

        & .rightArrow {
            @apply --arrow;
            right: 0;
            margin-right: var(--arrow-margin);
        }

        & .listStyleClass {
            display: flex;
            transition: transform .4s ease-in-out;
        }

        & .itemStyleClass {
            padding: 0px;
            display: inherit;
            flex-shrink: 0;
            overflow: hidden;
            
            @media (--viewport-sm) {
                width: 41.5%;
            }
            
            @media (--viewport-md) {
                width: 32.5%; /* Applied to get as per the mocks */
            }

            @media (--viewport-lg) {
                width: 22.22%; /* Applied to get as per the mocks */
            }

            @media (--viewport-xl) {
                width: 15.601%; /* Applied to get as per the mocks */
            }
        }

        & .desktopBodyClass {
            overflow: hidden;
        }

        & .mobileBodyClass{
            overflow-y: hidden;
            overflow-x: scroll;
            -webkit-overflow-scrolling: touch;
        }

        & .iconClass{
            fill: var(--color-seconday-turquoise);
            color: var(--color-seconday-turquoise);
        }

        & .disabledArrow{
            fill: var(--color-gray-3);
            color: var(--color-gray-3);
        }
    }
}



