@import "../../assets/styles/colors/colors.css";

.typeaheadInputContainer {
    border: 1px solid var(--color-gray);
    border-radius: 20px;
    text-align: center;
    background: var(--color-ghost-white);
    position: relative;

    & .inputFieldBlock {
        vertical-align: middle;
        background: none;
        width: 100%;
        border-radius: 20px;
        padding: 0 16px;
        position: relative;

        & .searchIconBlock {
            display: inline-block;
            vertical-align: middle;
            position: absolute;
            top: 50%;
            transition: all .25s;
            transform: translateY(-50%);
            z-index: 1;
        }

       & .resetIconBlock {
            display: inline-block;
            vertical-align: middle;
            position: absolute;
            top: 50%;
            transition: all .25s;
            transform: translateY(-50%);
            z-index: 1;
        }

        & .typeaheadFocusInputContent {
            display: inline-block;
            vertical-align: middle;
            transition: all 0.3s;
            font-weight: normal;
            margin-bottom: 0;
            width: 100%;

            & .input_default {
                display: block;
                margin-bottom: 0;
            }
            & .inputBox {
                -webkit-transition: all .3s;
                transition: all .3s;
                width: 100%;
                color: #888;
            }
        }

        &.typeaheadInputFieldBlurred {

            & .searchIconBlock {
              display: inline-block;
              vertical-align: middle;
              width: 1.5625rem;
              font-size: 1rem;
              position: absolute;
              -webkit-transition: all .25s;
              top: 50%;
              -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
              left: calc(50% - 25px);
              z-index: 2;
            }

            & .typeaheadFocusInputContent {
                & .inputBox {
                    padding: .75rem .875rem .75rem 50%;
                }
            }
        }

        &.typeaheadInputFieldFocused {
            background: var(--color-white);

            & .searchIconBlock {
                left: 12px;

            }

            & .resetIconBlock {
                right: 12px;
            }

            & .typeaheadFocusInputContent {
                padding-left: 10px;

                & .inputBox {
                    padding: .75rem;
                }
            }
        }
    }

    & .typeaheadSuggestionBox {
        position: absolute;
        background: var(--color-white);
        vertical-align: middle;
        padding: 0;
        border: 1px solid var(--color-gray);
        border-top: 0;
        border-radius: 0 0 5px 5px;
        width: calc(100% - 32px);
        z-index: 2;
        margin: 0;
        left: 16px;
        padding-left: rem(10);
        font-size: 13px;

        & .typeaheadTermBoxHeader {
            color: var(--color-dark-solid-gray);
            width: 100%;
            font-weight: 500;
            text-align: left;
            border-top: 1px solid var(--color-gray);
            display: table;
            font-weight: normal;
        }

        & .typeaheadDeptHeader {
            color: var(--color-dark-solid-gray);
            width: 100%;
            font-weight: normal;
            display: inline-block;
            text-align: left;
            border-top: 1px solid var(--color-gray);
        }
        & .typeaheadRecentListText {
            white-space: nowrap;
            width: 100%;
            padding: 12px;
            text-overflow: ellipsis;
            overflow: hidden;
            display: block;
        }
         & .typeaheadRecentListDeptText {
            white-space: nowrap;
            width: 100%;
            text-overflow: ellipsis;
            overflow: hidden;
            display: block;
            padding: 12px 12px 12px 24px;
        }
    }
}



